import React, { useEffect } from "react";

import "./AnimatedDesign.css";

import Navbar from "../../../components/genral_components/Navbar/Navbar";

import SubCategoriesBanner from "../../../templates/subcategories/subcategoriesbanner/SubCategoriesBanner";

import SubCategoriesCardLeft from "../../../templates/subcategories/sub-categories-card-left/SubCategoriesCardLeft";

import SubCategoriesCardRight from "../../../templates/subcategories/sub-categories-card-right/SubCategoriesCardRight";

import AnimatedDesignPageDescription from "../../../components/animated-design-page-components/animated-design-page-description/AnimatedDesignPageDescription";

import Pricing2 from "../../../components/services_page_components/pricing/Pricing2";

import Skills from "../../../components/home_page_components/ourskills/Skills";

import Ourwork from "../../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../../components/genral_components/Footer/Footer";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"

import { useLocation } from "react-router-dom"

import AnimatedDesignBanner from "./AnimatedDesignBanner.jpg"

export default function AnimatedDesign() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  
  return (
    <div className="animated-design-main-container">
      <Navbar />

      <SubCategoriesBanner
        bannerHeading="Animated Design"
        bannerParagraph="Ignite Your Website with Vibrant Animated Designs"
        bannerButton1="LEARN MORE"
        bannerButton2="OUR WORK"
        bannerBackgroundImage={AnimatedDesignBanner}
      />

      <AnimatedDesignPageDescription headingOne="At interac8, our team of skilled animators, graphic designers, and motion graphics artists work closely with clients to develop unique concepts and bring them to life through various animation techniques. We provide custom animations that complement the brand identity and goals of the customer." />

      <SubCategoriesCardLeft
        paragrapgh="We make sure that every component is meticulously developed to deliver a seamless and captivating experience, from concept creation and storyboarding to animation production and visual effects. Social media, websites, presentations, and mobile applications are just a few of the types and platforms for which our animations have been formatted and optimized. "
        button="LET'S CHAT"
        picture={pic1}
      />

      <SubCategoriesCardRight
        paragraph={`We value client input throughout the process and consider it to ensure that we are meeting their expectations. Our dedication to prompt delivery and continuing support makes sure that clients get the finished animation designs in the necessary formats and get help putting them into practice.\n\nOur aim is to provide animated graphics that are aesthetically arresting, powerful, and successfully convey messages to audiences.`}
        button="LET'S CHAT"
        picture={pic2}
      />

      <br />
      <br />
      <br />
      <br />

      <Pricing2
        numberOfCards={3}
        headings={["BASIC", "STANDARD", "PREMIUM"]}
        whichCardIsRecommended={1}
        prices={["$5 / sec", "$8 / sec", "$15 / sec"]}
        cardPointers={[
          [
            "Footage trimming and rearranging",
            "Color correction and grading",
            "Audio level adjustment and noise reduction",
            "Basic text titles and captions",
            "Simple transitions between clips",
            "Basic audio background music",
            "Expedited turnaround time",
            "Fast communication with clients"
          ],
          [
            "Advanced color correction and grading",
            "Multi-camera footage syncing and editing",
            "Audio mixing and sound design",
            "Motion graphics and visual effects",
            "Advanced text titles and captions",
            "Green screen keying and compositing",
            "Expedited turnaround time",
            "Fast communication with clients",
            "Flexible scheduling and extended working hours",
            "Dedicated team working on project"
          ],
          [
            "Script development and storyboarding",
            "Professional video filming with lighting and sound equipment",
            "Full-scale editing with advanced color grading and motion graphics",
            "Customized music and sound design",
            "Voiceover recording and integration",
            "Closed captioning and subtitles",
            "Expedited turnaround time",
            "Fast communication with clients",
            "Flexible scheduling and extended working hours",
            "Dedicated team working on project"
          ],
        ]}
      />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />
      <br />

      <Ourwork />

      <Footer />
    </div>
  );
}
